/* stylelint-disable */

:root {

  /* Colors */
  --color-mi-blau: #4952e1;
  --color-mi-pink: #d16;
  --color-mi-gruen: #00ad2f;
  --color-mi-lila: #9313ce;
  --color-mi-black: #231f20;
  --color-mi-grau: #aaa;
  --color-cg-pink: rgb(240 15 210);
  --color-cg-gruen: rgb(5 210 138);
  --color-mi-hellgrau: #efefef;
  --color-light: hsl(0deg 0% 80%);
  --color-lighter: hsl(0deg 0% 90%);
  --color-lightest: hsl(0deg 0% 100%);
  --color-dark: hsl(0deg 0% 20%);
  --color-darker: hsl(0deg 0% 10%);
  --color-darkest: hsl(0deg 0% 0%);
  --color-darken: hsl(0deg 0% 0%/ 50%);
  --color-darken-strong: hsl(0deg 0% 0%/ 80%);
  --color-lighten: hsl(0deg 0% 100%/ 50%);
  --color-lighten-strong: hsl(0deg 0% 100%/ 80%);

  /* Fonts */
  --ff-headlines: barlow, arial, helvetica, sans-serif;
  --ff-primary: barlow, arial, helvetica, sans-serif;
  --ff-code: 'Fira Code', "Courier New", "Courier", monospace;
  --ff-icons: material-icons;

  /* Font weights */
  --fw-light: 200;
  --fw-normal: 400;
  --fw-medium: 500;
  --fw-bold: 600;

  /* Line heights */
  --lh-tight: 100%;
  --lh-normal: 130%;
  --lh-loose: 150%;

  /* Sizes */
  /* Renard 10 */
  --basefont-size: 1rem;
  --size-xs: calc(var(--basefont-size) * 0.25);
  --size-s: calc(var(--basefont-size) * 0.6);
  --size-m: calc(var(--basefont-size) * 1);
  --size-l: calc(var(--basefont-size) * 1.5);
  --size-xl: calc(var(--basefont-size) * 2.2);
  --size-xxl: calc(var(--basefont-size) * 3);
  --size-xxxl: calc(var(--basefont-size) * 4);
  --size-eighth: calc(var(--size-m) / 8);
  --size-quarter: calc(var(--size-m) / 4);
  --size-half: calc(var(--size-m) / 2);

  /* Transitions */
  --tr-fast: 0.3s;
  --tr-normal: 0.6s;
  --tr-slow: 1s;

  /* Box Sizes */
  --max-text-width: 40em;
  --max-content-width: 60em;

  /* Buttons */
  --button-border-width: var(--border-width);
  --button-border-color: var(--color-cg-pink);
  --button-text-color: var(--color-lightest);
  --button-bg-color: var(--color-darkest);
  --button-bg-hover-color: var(--color-lighten-strong);

  /* Misc */
  --border-width: 2px;
  --level-bg: 0;
  --level-fg: 10;
  --radius-m: 3px;
  --text-gradient: linear-gradient(90deg,var(--color-cg-pink) , var(--color-cg-gruen));
  --text-gradient-reversed: linear-gradient(270deg,var(--color-cg-pink) , var(--color-cg-gruen));
  --box-shadow: 0 0 1em #000;
}